const axios = require('axios');

class Students {
    static async all() {
        let res = await axios.get('../data/students-scoress.json').then(res => res.data);
        return res;
     }
}

class Classes {
    static async all() {
       let res = await axios.get('../data/class-scores.json').then(resp => resp.data);
       return res;
     }
}
class Schools {
    static async all() {
       let res = await axios.get('../data/school-scores.json').then(resp => resp.data);
       return res;
     }
}


jest.mock('axios');

test('Should fetch Students', () => {
    const students = [{
        "school_name": "state central",

        "score": 995,
        "class_name": 3
    }];

    const resp = { data : students };

    axios.get.mockImplementation(() => Promise.resolve(res));
    
    return Students.all().then(data => expect(resp.data).toEqual(students));
});

